/* ... Other styles for components */
/* Dark/Light mode toggle button styles */

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #fff;
  transition: 0.4s;
  border-radius: 50%;
}
input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}
input:checked + .square{
  background-color: #2196f3;
  color: #ccc;
}
input:checked + .slider:before {
  transform: translateX(26px);
}
.board{
  width: 60vw; /* 60% of viewport width */
  max-width: 600px; /* Maximum width for the board */
  margin: 0 auto;
}
/* HTML and Body */
.dark-light-toggle{
  padding-top: 3dvh;
  padding-bottom: 0;
  padding-left: 1dvw;
}
.board-container {
  justify-content: center;
  margin-top: 15dvh;
  margin-left: 8dvw; /* Adjust spacing around the board */
  max-width: 90%;
  padding-top: 6dvh;
  margin-right: 8dvw;
  padding-bottom: 2dvh;
}
.status{
  font-size: 5dvh;
  font-family: georgia;
  justify-content: center;
}
.board-row{
  display: flex;
  justify-content: space-around;
  margin: 3vh 0;
}
.square{
  width: 28%;
  height: 28%;
  font-size: 6vh;
  background-color: #f3f7f6;
}
.Res{
  width: 26%;
  display: inline-block;
  justify-content:stretch;
  margin-left: 36%;
  margin-top: 8dvh;
  height: 50%;
  font-size: 100%;
  background-color: rgb(228, 117, 100);
  border-radius: 35px;
}
@keyframes confetti {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  100% {
    transform: translateY(100vh) rotate(360deg);
  }
}

.confetti {
  position: fixed;
  width: 1dvh;
  height: 2dvw; /* Replace with your confetti color */
  animation: confetti 1s linear infinite;
  pointer-events: none;
}